import React, { useState, useEffect } from 'react';
import MainCard from '../../../../ui-component/cards/MainCard';
import ConfirmDialog from '../../../../ui-component/ConfirmDialog/ConfirmDialog';
import { makeStyles, withStyles } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import FavoriteIcon from '@material-ui/icons/Favorite';
import { useDispatch, useSelector } from 'react-redux';
import { getWishlist, deleteBookFromWishlist, moveToCart } from '../../../../store/actions/WishlistActions';
import { useParams } from 'react-router-dom';
import useUserRole from '../../../../hooks/useUserRole';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const useStyles = makeStyles((theme) => ({
    table: {
        minWidth: 650
    },
    tableContainer: {
        borderRadius: 15,
        marginTop: 16
    },
    tableHeaderCell: {
        fontWeight: 'lighter',
        backgroundColor: theme.palette.primary.light,
        color: '#ffffff'
    },
    status: {
        fontWeight: 'lighter',
        fontSize: '0.75rem',
        color: 'white',
        backgroundColor: 'gray',
        borderRadius: 8,
        padding: '3px 10px',
        display: 'inline-block'
    },
    root: {
        padding: 16,
        width: '100%'
    },
    btn: {
        cursor: 'pointer',
        boxShadow: '0 0.5em 0.5em 0.1em #68c2d9',
        marginLeft: 20
    },
    titleContainer: {
        background: theme.palette.primary.main,
        padding: 10,
        borderRadius: 8,
        marginBottom: 20
    },
    pageTitle: {
        fontSize: 16,
        color: theme.palette.background.paper,
        fontWeight: 'lighter'
    },

    viewIcon: {
        verticalAlign: 'sub',
        background: 'tranparent',
        color: '#fff'
    },
    searchControl: {
        width: '234px',
        marginLeft: '16px',
        paddingRight: '16px',
        paddingLeft: '16px',
        '& input': {
            background: 'transparent !important',
            paddingLeft: '5px !important'
        },
        [theme.breakpoints.down('lg')]: {
            width: '250px'
        },
        [theme.breakpoints.down('md')]: {
            width: '100%',
            marginLeft: '4px',
            background: '#fff'
        }
    },
    startAdornment: {
        fontSize: '1rem',
        color: theme.palette.grey[500]
    }
}));

export default function ViewWishlist() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    let { id } = useParams();
    const [value, setValue] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const { getWishlistData } = useSelector((state) => state?.wishlist);
    const newData = getWishlistData?.content;
    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });

    const handleDeleteBook = async (wishlistId, bookId) => {
        dispatch(
            deleteBookFromWishlist(wishlistId, bookId, () => {
                dispatch(getWishlist(id));
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
    };
    
    const handleMoveToCart = (bookId) => {
        dispatch(
            moveToCart(id, bookId, () => {
                dispatch(getWishlist(id));
                
            })
        );
        // window.location.reload();
    };
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    useEffect(() => {
        dispatch(getWishlist(id, value ? value : ''));
    }, [value]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <FavoriteIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Wishlist Details </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid style={{ display: 'flex', justifyContent: 'flex-start' }}>
                        <div>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                onChange={(e) => setValue(e?.target?.value)}
                                placeholder="Search"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </div>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Cover </TableCell>
                                    <TableCell> Book Title </TableCell>
                                    <TableCell> </TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell>
                                                <img src={row?.bookDetail?.imageUrls} alt="book" width="60px" />
                                            </TableCell>
                                            <TableCell>{row?.bookDetail?.title}</TableCell>
                                            <TableCell>
                                                <Button variant="outlined" onClick={() => handleMoveToCart(row?.bookDetail?.id)}>
                                                    Move to cart
                                                </Button>
                                            </TableCell>
                                            <TableCell align="right">
                                                <IconButton
                                                    onClick={() => {
                                                        setConfirmDialog({
                                                            isOpen: true,
                                                            title: 'Delete Cart',
                                                            subTitle: 'Are you sure you want to delete this book.',
                                                            onConfirm: () => {
                                                                handleDeleteBook(row?.wishListId, row?.bookDetail?.id);
                                                            }
                                                        });
                                                    }}
                                                >
                                                    <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                </IconButton>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={newData?.length || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
